import React, { useState } from 'react'
import styles from '../scss/Cart.module.scss'
import Uptitle from '../component/Uptitle'
import { useDispatch, useSelector } from 'react-redux'
import {deleteItem} from "../store.js"
import { useNavigate } from 'react-router-dom'


const Cart = () => {
  const state = useSelector((state)=>{
    return state
  })
  const cart = state.cart;

  let navigate = useNavigate();
  let dispatch = useDispatch()
  let cart_delete = () =>{
      dispatch(deleteItem);
  }
  //총가격
  let total = 0;
  for(var i=0;i<cart.length;i++){
    total= total + cart[i].price;
  }
  //원단위 따옴표 만들기
  let unit = (val) =>{ 
    return String(val).replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,');
  }
  return (
    <>
        <div className={styles.wrap}>
          <div className={styles.title}>
              <Uptitle h1 ="장바구니" p=""/>
          </div>
          <div className={styles.content_box}>
              <form name="cart" id="cart" method='post'>
                <div className={styles.table_box}>
                  <table>
                    <thead>
                      <tr>
                        <th><input type="checkbox"/></th>
                        <th>작품</th>
                        <th>단가</th>
                        <th>갯수</th>
                        <th>소계</th>
                      </tr>
                    </thead>
                    <tbody>
                    {
                      cart.map((v,i) =>
                      <tr key={i}> 
                        <td><input type="checkbox"/></td>
                        <td className={styles.table_tbody_pro}>
                          <div className={styles.table_img} style={{backgroundImage:`url(${process.env.PUBLIC_URL+v.imgUrl})`}}>
                          </div>
                          <div className={styles.table_artdetail}>
                            <div className={styles.table_title}>
                              <h4>{v.title}</h4><p>{v.artist}<span>&nbsp;&nbsp;(+{unit(v.oriprice)}원)</span></p>
                            </div>
                            <p>사이즈 : {v.size}<span>&nbsp;(+{unit(v.sizeadd)}원)</span><br/>
                              미디어 : {v.media}<span>&nbsp;(+{unit(v.mediaadd)}원)</span><br/>
                              프레임 : {v.flame}<br/>
                              매트(여백) : {v.matt}</p>
                          </div>
                        </td>
                        <td><p>{unit(v.oriprice+v.sizeadd+v.mediaadd)}원</p></td>
                        <td><p>{v.number}</p></td>
                        <td><p>{unit(v.price)}원</p></td>
                      </tr>
                      )
                     }
                    </tbody>
                  </table>
                  <div className={styles.table_button}>
                    <button>선택삭제</button>
                    <button onClick = {() => {cart_delete(); navigate("/cart");}} >장바구니 비우기</button>
                  </div>
                </div>
              </form>
              <div className={styles.totalpirce}>
                <ul>
                  <li>
                    <span>총 상품금액</span>
                    <strong>{unit(total)}</strong>
                    <span>원</span>
                  </li>
                  <li>
                    <i>+</i>
                    <span>배송비</span>
                    <strong>0</strong>
                    <span>원</span>
                  </li>
                  <li>
                    <i>=</i>
                    <span>총 결제금액</span>
                    <strong>{unit(total)}</strong>
                    <span>원</span>
                  </li>
                </ul>
              </div>
              <div className={styles.final_button}>
                <button>쇼핑 계속하기</button>
                <button>선택상품 주문</button>
              </div>
          </div>
        </div>
    </>
  )
}

export default Cart