import {configureStore, createSlice} from '@reduxjs/toolkit'
import data from './data.js';
import data2 from './data2.js';

let art = createSlice({
    name:'art',
    initialState:data
})
let artist = createSlice({
    name:'artist',
    initialState:data2
})

let cart = createSlice({
    name:'cart',
    initialState:[],

    reducers : {
        addItem(state,action){
            state.push(action.payload);
        },
        deleteItem(state){
            state = [];
        }
    }
})
export let {addCount,addItem,sortName,deleteItem} = cart.actions


export default configureStore({
    reducer:{
        art:art.reducer,
        artist:artist.reducer,
        cart:cart.reducer
    }
})