import styled from '@emotion/styled'


export const Title = styled.div`
width:500px;
display:flex;
flex-direction:column;
align-items:center;
font-family: 'EBSjusigyeongB';
opacity: 0; 
transform: translateY(50px);
transition:1.2s all ease;
margin:0 auto;
span{
    font-size:1.5rem
    margin-bottom:10px;
    img{
        width:60px;
        margin-right:10px;
    }
}
h1{
    width:100%;
    display:flex;
    flex-basis:100%;
    align-items:center;
    margin:8px 0px;
    font-size:3rem
}
h1::before{
    content:"";
    flex-grow:1;
    margin:0px 30px;
    height:10px;
    font-size:0px;
    line-height:0px;
    background-image: url("/img/sun4.png");
    background-repeat : no-repeat;

}
h1::after{
    content:"";
    flex-grow:1;
    margin:0px 30px;
    height:10px;
    font-size:0px;
    line-height:0px;
    background-image: url("/img/sun4.png");
    background-repeat : no-repeat;
    transform: scaleX(-1);
}
@media all and (768px <= width < 1149px) {
    h1{
        font-size:2.2rem
    }
    h1::after{
        opacity:0;
    }
    h1::before{
        opacity:0;
    }
}
@media all and (width < 767px) {
    width:100%;
    h1{
        font-size:2.2rem
    }
    h1::after{
        opacity:0;
    }
    h1::before{
        opacity:0;
    }
  }
`

