export const DetailMenu = styled.div`
border-bottom: 1px solid #dddddd;
margin: 100px 0;
ul{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    max-width: 1400px;
    width: 100%;
    li{
        a{  
            display:flex;
            justify-content:center;
            align-items:center;
            width:351px;
            height:65px;
            font-size:1.4rem;
        }
        .on{  
            display:flex;
            justify-content:center;
            align-items:center;
            width:351px;
            height:65px;
            font-size:1.4rem;
            color: #CC2F17;
            border-bottom:1px solid #CC2F17;
        }
    }
}

@media all and ( 0px <width < 767px) {
    margin: 50px 0;
    ul{
        width:100%;
        li{
            width:100%;
            a{  
                width:100%;
                height:40px;
                font-size:1rem;
            }
            .on{
                width:100%;
                height:40px;
                font-size:1rem;
            }
        }
    }
}
@media all and ( 768px <width < 1149px) {
    margin: 50px 0;
    ul{
        width:100%;
        li{
            width:100%;
            a{  
                width:100%;
                height:40px;
                font-size:1.3rem;
            }
            .on{
                width:100%;
                height:40px;
                font-size:1.3rem;
            }
        }
    }
}
`
export const Button10 = styled.div`
    width: 100px;
    text-align: center;
    position:relative;
    .btn-6 {
        width: 100%;
        height: 40px;
        line-height: 42px;
        padding: 0;
        border: none;
      }
      .btn-6 span {
        position: relative;
        display: block;
        width: 100%;
        height: 100%;
        font-family:'ON Yeossihyangyakeonhae B';
      }
      .btn-6:before,
      .btn-6:after {
        position: absolute;
        content: "";
        height: 0%;
        width: 2px;
        background-image: url("/img/sun2.png"); 
        background-position:center;
        background-repeat:no-repeat;
        background-size:cover;
      }
      .btn-6:before {
        right: 0;
        top: 0;
        transition: all 0.1s ease;
      }
      .btn-6:after {
        left: 0;
        bottom: 0;
        transition: all 0.1s 0.2s ease;
      }
      .btn-6:hover:before {
        transition: all 0.1s 0.2s ease;
        height: 100%;
        display:none;
      }
      .btn-6:hover:after {
        transition: all 0.1s ease;
        height: 100%;
        display:none;
      }
      .btn-6 span:before,
      .btn-6 span:after {
        position: absolute;
        content: "";
        background-image: url("/img/sun3_100.png"); 
        background-repeat:no-repeat;

      }
      .btn-6 span:before {
        left: 0;
        bottom: 0;
        width: 0%;
        height: 6px;
        transition: all 0.1s 0.1s ease;
      }
      .btn-6 span:after {
        left: 0;
        bottom: 0;
        width: 100%;
        height: 6px;
        display:none;

      }
      .btn-6 span:hover:before {
        width: 100%;
      }
`

