import React, { useEffect, useRef, useState} from 'react'
import styles from '../scss/Header.module.scss'
import { useNavigate } from 'react-router-dom';


const Header = () => {
    let navigate = useNavigate();
    const [isActive ,setIsActive] = useState(false);
    const [isMenuON ,setIsMenuON] = useState(false);

    const handleClick = event => {
        setIsActive(current => !current);
      };

    const menuHover = e => {
        const nodes = [...e.target.parentElement.children];
        const index = nodes.indexOf(e.target);
        setIsMenuON(index);
    }
    const menuOut = e => {
        setIsMenuON(999);
    }
    const nav = useRef();

    const wheel = (e) => {
        if(e.deltaY > 0){
            if(window.scrollY>50){
                nav.current.style.top = "-100px";
            }
        }
        if(e.deltaY < 0){
            nav.current.style.top = "0px";
            if(window.scrollY>100){
                nav.current.style.boxShadow = "1px 1px 5px rgba(0,0,0,0.1)";
            }else{
                nav.current.style.boxShadow = "none";
            }
        }
    }
    useEffect(() => {
        window.addEventListener("wheel", wheel);
    }, []);

  return (
    <>
        <nav>
            <div className={styles.navbox} ref={nav}>
                <div className={styles.ul1}>
                    <a href='#'><img src='/img/ico-header-menu.png' onClick={handleClick} style={{marginRight:"40px"}}/></a>
                    <ul>
                        <li><a href='#'>사진작품</a></li>
                        <li><a href='#'>그림작품</a></li>
                        <li><a href='#'>작가별</a></li>
                        <li><a href='#'>장르별</a></li>
                        <li><a href='#'>주제별</a></li>
                    </ul>
                </div>
                <div className={styles.logo} ref={nav}>
                        <a href='#'><img src='/img/logo3.png' onClick={()=>navigate('/')} /></a>
                </div>

                <div className={styles.ul1}>
                    <ul>
                        <li><a href='#'>로그인</a></li>
                        <li><a href='#'>회원가입</a></li>
                        <li><a href='#'>주문조회</a></li>
                        <li><a href='/cart'>장바구니</a></li>
                        <li><a href='#'>고객센터</a></li>
                    </ul>
                    <li><a href='#'><img src='/img/ico-header-search.png' /></a></li>
                </div>
            </div>
        </nav>
        
            <div className={isActive ? styles.left_header_wrap : styles.hide}>
                <div className={styles.left_header}>
                    <div className={styles.left_header_box}>
                        <div className={styles.left_header_logo}>
                            <a href='#'><img className={styles.menu_logo} src='/img/logo3.png' /></a>
                            <a href='#'><img onClick={handleClick} src='/img/ico-sitemap-close.png' /></a>
                        </div>
                        <div className={styles.left_header_con}>
                            <ul  onMouseLeave={menuOut}>
                                <li onMouseEnter={menuHover}>
                                    <a href="#">작품</a>
                                    <ul className={isMenuON === 0 ? styles.on : ""}>
                                        <li>베스트</li>
                                        <li>신상품</li>
                                        <li>사진작품</li>
                                        <li>그림작품</li>
                                        <li>작가별</li>
                                        <li>장르별</li>
                                        <li>주제별</li>
                                    </ul>
                                </li>
                                <li onMouseEnter={menuHover}>
                                    <a href="#">회사소개</a>
                                    <ul className={isMenuON === 1 ? styles.on : ""}>
                                        <li>회사소개</li>
                                        <li>갤러리</li>
                                    </ul>
                                </li>
                                <li onMouseEnter={menuHover}>
                                <a href="#">커뮤니티</a>
                                    <ul className={isMenuON === 2 ? styles.on : ""}>
                                        <li>공지사항</li>
                                        <li>이벤트</li>
                                    </ul>
                                </li>
                                <li onMouseEnter={menuHover}>
                                <a href="#">서비스</a>
                                    <ul className={isMenuON === 3 ? styles.on : ""}>
                                        <li>FAQ</li>
                                        <li>제휴협찬문의</li>
                                        <li>1:1문의</li>
                                    </ul>
                                </li>
                            </ul>
                        </div>

                    </div>
                </div>
            </div>
        
    </>
  )
}

export default Header
