import React, { useEffect, useState ,useRef} from 'react'
import styles from '../scss/Content.module.scss'
import {Title,Button10} from '../styles/emotion'
import { Nav } from  'react-bootstrap';
import { useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';

const Content = () => {
  let navigate = useNavigate();
  let state = useSelector((state)=>{
    return state
  })

  const art = state.art;

  const upTitle1 = useRef();
  const upTitle2 = useRef();
  const upTitle3 = useRef();
  const upTitle4 = useRef();
  const upEle1 = useRef();
  const upEle2 = useRef();
  const upEle3 = useRef();
  const upEle4 = useRef();
  const upEle5 = useRef();
  const upeleGroup = [upEle1,upEle2,upEle3,upEle4,upEle5];
  const upTitleGroup = [upTitle1,upTitle2,upTitle3,upTitle4];

  //배경 이미지 변경 
  const [isSecON ,setIsSecON] = useState(1);
  var num = 2;
  const num_up = () => {
    if(num<4){
      setIsSecON(num);
    }
    num = num+1;
    if(num>=4){
      num = num=1;
    }
  }
  
    useEffect(() => {
      setInterval(num_up,3800); //섹션 시간마다 반복
      window.addEventListener("scroll", upElement); //글자 올라오기 애니
      window.addEventListener("wheel", wheel); //글자효과
      window.addEventListener("resize", upElement);
  }, []);

  const upElement = () => {
    var select = new Up_opacity();
    var select2 = new Up_opacity();
    for(var i =0; i<upeleGroup.length; i++){
      select.up(upeleGroup[i]);
    }
    for(var i =0; i<upTitleGroup.length; i++){
      select2.up_widen(upTitleGroup[i]);
    }
  };

  function Up_opacity(){
    this.scrollY = window.scrollY;
  }
  Up_opacity.prototype.up = function(ele){
    if(ele.current != null){
      this.selector = ele.current;
      this.scroll = this.selector.offsetTop - window.innerHeight;
      if(this.scrollY > this.scroll){
          this.selector.style.opacity = 1;
          this.selector.style.transform ="translateY(0px)";
      }
    }
  }
  Up_opacity.prototype.up_widen = function(ele){
    if(ele.current != null){
      this.selector = ele.current;
      this.scroll = this.selector.offsetTop - window.innerHeight;
      if(this.scrollY > this.scroll){
          this.selector.style.opacity = 1;
          this.selector.style.transform ="translateY(0px)";
          if(window.innerWidth>1149){
            this.selector.style.width = "800px";
          }else{
            this.selector.style.width = "100%";
          }
          this.selector_child = this.selector.children[1];
      }
    }
  } 
  //글자 효과
  const direct_gallery_h1 = useRef();
  const wheel = () =>{
    if(direct_gallery_h1.current !=null){
      const h1_child = direct_gallery_h1.current.children;
      if(window.scrollY > 3600){
          for(var i=0;i<h1_child.length;i++){
            h1_child[i].style.opacity = 1;
            h1_child[i].style.visibility = "visible";
            h1_child[i].style.transform = "rotateY(0)";
            h1_child[i].style.transitionDelay = i*0.1+"s";

          }
      }
    }
  }
  //가격 따옴표
  let unit = (val) =>{
    return String(val).replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,');
  }
  return (
    <>
      <div className={styles.wrap}>
        <section>
            <div className={styles.slider}>
              <ul className={styles.imgs}>
                  <li className={isSecON === 1 ? styles.ss : ""} >
                    <div className={isSecON === 1 ? styles.sliderCon_on :styles.sliderCon }>
                      <h1>그<br/>랜<br/>드<br/>오<br/>픈<br/></h1>
                      <h4>갤<br/>러<br/>리<br/>샵<br/></h4>
                    </div>
                    <img src="/img/2b745dcd4730d.jpg" />
                  </li>
                  <li className={isSecON === 2 ? styles.ss : ""}>
                    <div className={isSecON === 2 ? styles.sliderCon_on :styles.sliderCon }>
                        <h1>그<br/>랜<br/>드<br/>오<br/>픈<br/></h1>
                        <h4>내<br/>가<br/>그<br/>린<br/>그<br/>림<br/></h4>
                      </div>
                    <img src="/img/Hyewon-Danopungjeong.jpg" />
                  </li>
                  <li className={isSecON === 3 ? styles.ss : ""}>
                    <div className={isSecON === 3 ? styles.sliderCon_on :styles.sliderCon }>
                        <h1>내<br/>가<br/>그<br/>린<br/>그<br/>림<br/></h1>
                        <h4>디<br/>자<br/>인<br/>in<br/></h4>
                      </div>
                    <img src="/img/so.jpg" />
                  </li>
              </ul>
              <div className={styles.pagination}>
                <div className={isSecON === 1 ? styles.pagination_con_on : styles.pagination_con}>
                    <p>01</p>
                    <h6>내가그린그림 갤러리샵<br/>그랜드 오픈</h6>
                </div>
                <div className={isSecON === 2 ? styles.pagination_con_on : styles.pagination_con}>
                    <p>02</p>
                    <h6>그랜드 오픈</h6>
                </div>
                <div className={isSecON === 3 ? styles.pagination_con_on : styles.pagination_con}>
                    <p>03</p>
                    <h6>내가그린그림</h6>
                </div>
              </div>
            </div>
        </section>
        <article>
          <div className={styles.best}>
            <Title ref={upTitle1}>
              <span className={styles.title_h1}><img src='/img/logo3.png'/>에서 가장 인기있는 작품을 소개합니다.</span>
              <h1 className={styles.title_span}>최고의 작품</h1>
            </Title>
            <div className={styles.best_container} ref={upEle1}>
              <div className={styles.best_box}>
                <Nav.Link onClick={()=>navigate('/detail/0')}>
                  <img src={process.env.PUBLIC_URL+art[0].imgUrl} className={styles.best_img}/>
                  <div className={styles.best_con}>
                    <div className={styles.best_sub_title}>
                      <h3>{art[0].title}</h3>
                    </div>
                    <div className={styles.best_detail}>
                      <p>{art[0].artist}</p>
                      <p>30.0cm x 30.0cm</p>
                    </div>
                    <div className={styles.best_price}>
                      <h3>{unit(art[0].price)}<span>원</span></h3>
                    </div>
                  </div>
                </Nav.Link>
              </div>
              <div className={styles.best_box}>
                <Nav.Link onClick={()=>navigate('/detail/6')}>
                <img src={process.env.PUBLIC_URL+art[6].imgUrl} className={styles.best_img} />
                <div className={styles.best_con}>
                  <div className={styles.best_sub_title}>
                    <h3>{art[6].title}</h3>
                  </div>
                  <div className={styles.best_detail}>
                    <p>{art[6].artist}</p>
                    <p>30.0cm x 30.0cm</p>
                  </div>
                  <div className={styles.best_price}>
                    <h3>{unit(art[6].price)}<span>원</span></h3>
                  </div>
                </div>
                </Nav.Link>
              </div>
              <div className={styles.best_box}>
                <Nav.Link onClick={()=>navigate('/detail/4')}>
                <img src={process.env.PUBLIC_URL+art[4].imgUrl}  className={styles.best_img} />
                <div className={styles.best_con}>
                  <div className={styles.best_sub_title}>
                    <h3>{art[4].title}</h3>
                  </div>
                  <div className={styles.best_detail}>
                    <p>{art[4].artist}</p>
                    <p>30.0cm x 30.0cm</p>
                  </div>
                  <div className={styles.best_price}>
                    <h3>{unit(art[4].price)}<span>원</span></h3>
                  </div>
                </div>
                </Nav.Link>
              </div>
              <div className={styles.best_box}>
                <Nav.Link onClick={()=>navigate('/detail/1')}>
                <img src={process.env.PUBLIC_URL+art[1].imgUrl} className={styles.best_img} />
                <div className={styles.best_con}>
                  <div className={styles.best_sub_title}>
                    <h3>{art[1].title}</h3>
                  </div>
                  <div className={styles.best_detail}>
                    <p>{art[1].artist}</p>
                    <p>30.0cm x 30.0cm</p>
                  </div>
                  <div className={styles.best_price}>
                    <h3>{unit(art[1].price)}<span>원</span></h3>
                  </div>
                </div>
                </Nav.Link>
              </div>
            </div>
          </div>
          <div className={styles.pick}>
            <Title ref={upTitle2}>
                <span className={styles.title_h1}><img src='/img/logo3.png'/>이 선정한 추천 작품을 만나보세요.</span>
                <h1 className={styles.title_span}>선정 작품</h1>
            </Title>
            <div className={styles.pick_tab_box}>
              <div className={styles.pick_tab} ref={upEle2}>
                <div className={styles.pick_tab_painting}>
                  <h5>그림작품</h5>
                </div>
                <h5 style={{margin:"0px 10px", color:"gray"}}>|</h5>
                <div className={styles.pick_tab_picture}>
                  <h5>사진작품</h5>
                </div>
              </div>
              <div className={styles.pick_content} ref={upEle3}>
                  <div className={styles.pick_content_box} ref={upEle3}>
                    <img src={art[2].imgUrl} className={styles.pick_content_painting} />
                    <div className={styles.pick_content_text}>
                      <div className={styles.pick_painting_artist}>
                        <p>{art[2].artist}</p>
                      </div>
                      <div className={styles.pick_painting_title}>
                        <h1>{art[2].title}</h1>
                      </div>
                      <div className={styles.pick_painting_con}>
                        <p>{art[2].content1}</p>
                      </div>
                      <Button10>
                        <div className="btn-6">
                          <span>더보기</span>
                        </div>
                      </Button10>
                    </div>
                  </div>
              </div>
            </div>
          </div>
          <div className={styles.newart}>
            <Title ref={upTitle3}>
                <span className={styles.title_h1}><img src='/img/logo3.png'/>과 함께하는 새로운 작품을 소개합니다.</span>
                <h1 className={styles.title_span}>새로운 작품</h1>
            </Title>
            <div className={styles.newart_container} ref={upEle4}>
              <div md={4} className={styles.newart_box}>
                <Nav.Link onClick={()=>navigate('/detail/3')}>
                <img src={process.env.PUBLIC_URL+art[3].imgUrl} className={styles.newart_img} />
                <div className={styles.newart_con}>
                  <div className={styles.newart_artist}>
                    <h6>{art[3].artist}</h6>
                  </div>
                  <div className={styles.newart_title}>
                    <h3>{art[3].title}</h3>
                  </div>
                  <div className={styles.newart_detail}>
                    <p>{art[3].content1}</p>
                  </div>
                </div>
                </Nav.Link>
              </div>
              <div md={4} className={styles.newart_box}>
                <Nav.Link onClick={()=>navigate('/detail/5')}>
                <img src={process.env.PUBLIC_URL+art[5].imgUrl} className={styles.newart_img} />
                <div className={styles.newart_con}>
                  <div className={styles.newart_artist}>
                    <h6>{art[5].artist}</h6>
                  </div>
                  <div className={styles.newart_title}>
                    <h3>{art[5].title}</h3>
                  </div>
                  <div className={styles.newart_detail}>
                    <p>{art[5].content2}</p>
                  </div>
                </div>
                </Nav.Link>
              </div>
              <div md={4} className={styles.newart_box}>
                <Nav.Link onClick={()=>navigate('/detail/7')}>
                <img src={process.env.PUBLIC_URL+art[7].imgUrl} className={styles.newart_img} />
                <div className={styles.newart_con}>
                  <div className={styles.newart_artist}>
                    <h6>{art[7].artist}</h6>
                  </div>
                  <div className={styles.newart_title}>
                    <h3>{art[7].title}</h3>
                  </div>
                  <div className={styles.newart_detail}>
                    <p>{art[7].content1}</p>
                  </div>
                </div>
                </Nav.Link>
              </div>
            </div>
          </div>
          <div className={styles.direct_gallery}>
            <div className={styles.direct_gallery_con}>
              <div className={styles.direct_gallery_title}>
                <h1 className={styles.direct_gallery_h1} ref={direct_gallery_h1}>
                  <p>내</p>
                  <p>가</p>
                  <p>그</p>
                  <p>린</p>
                  <p>그</p>
                  <p>린</p>
                  <p>그</p>
                  <p>림</p>
                  <p> </p>
                  <p>갤</p>
                  <p>러</p>
                  <p>리</p>
                </h1>
              </div>
              <div className={styles.direct_gallery_detail}>
                <h5>갤러리에서 다양한 그림을 직접 감상하시고 구입하세요.</h5>
              </div>
              <Button10>
                <div className="btn-6">
                  <span>더보기</span>
                </div>
              </Button10>
            </div>
          </div>
          <div className={styles.review}>
            <Title ref={upTitle4}>
                <span className={styles.title_h1}><img src='/img/logo3.png'/>의 작품을 이용한 고객들의 실제 후기입니다.</span>
                <h1 className={styles.title_span}>후기</h1>
            </Title>
                <div className={styles.review_container} ref={upEle5}>
                  <div md={4} className={styles.review_box}>
                    <div className={styles.review_img} style={{backgroundImage:`url(${process.env.PUBLIC_URL+art[8].imgUrl})`}}>
                        <span className={styles.review_date}>
                            <span>10월</span>
                            <h5>02</h5>
                        </span>
                    </div>
                    <div className={styles.review_con}>
                      <div className={styles.review_first}>
                        <div className={styles.review_artist}><p>{art[8].artist}</p></div>
                        <span>|</span>
                        <div className={styles.review_title}><p>{art[8].title}</p></div>
                      </div>
                      <div className={styles.review_detail}>
                        <p>인테리어에 효과 톡톡히 봤어요~!</p>
                      </div>
                      <div className={styles.review_button}>
                      <Button10>
                        <div className="btn-6">
                          <span>더보기</span>
                        </div>
                      </Button10>
                      </div>
                    </div>
                  </div>
                  <div md={4} className={styles.review_box}>
                    <div className={styles.review_img} style={{backgroundImage:`url(${process.env.PUBLIC_URL+art[9].imgUrl})`}}>
                      <span className={styles.review_date}>
                            <span>08월</span>
                            <h5>13</h5>
                      </span>
                    </div>
                    <div className={styles.review_con}>
                      <div className={styles.review_first}>
                        <div className={styles.review_artist}><p>{art[9].artist}</p></div>
                        <span>|</span>
                        <div className={styles.review_title}><p>{art[9].title}</p></div>
                      </div>
                      <div className={styles.review_detail}>
                        <p>그림퀄리티가 아주 맘에 드네요</p>
                      </div>
                      <div className={styles.review_button}>
                      <Button10>
                        <div className="btn-6">
                          <span>더보기</span>
                        </div>
                      </Button10>
                      </div>
                    </div>
                  </div>
                  <div md={4} className={styles.review_box}>
                    <div className={styles.review_img} style={{backgroundImage:`url(${process.env.PUBLIC_URL+art[1].imgUrl})`}}>
                      <span className={styles.review_date}>
                            <span>02월</span>
                            <h5>25</h5>
                      </span>
                    </div>
                    <div className={styles.review_con}>
                      <div className={styles.review_first}>
                        <div className={styles.review_artist}><p>{art[1].artist}</p></div>
                        <span>|</span>
                        <div className={styles.review_title}><p>{art[1].title}</p></div>
                      </div>
                      <div className={styles.review_detail}>
                        <p>배송 포장 모두 만족합니다.<br/>
                           그림 퀄리티는 말할 것도 없고,색상도 선명하고 무광이라 반사도...
                        </p>
                      </div>
                      <div className={styles.review_button}>
                      <Button10>
                        <div className="btn-6">
                          <span>더보기</span>
                        </div>
                      </Button10>
                      </div>
                    </div>
                  </div>
              </div>
          </div>
        </article>
      </div>
    </>
  )
}

export default Content
