import React, { useEffect, useRef } from 'react'
import { Title } from '../styles/emotion';

const Uptitle = (props) => {
    const upTitle = useRef();
    useEffect(() => {
         upElement();
    }, []);
  
    const upElement = () => {
      var select2 = new Up_opacity();
        select2.up_widen(upTitle);
    };
  
    function Up_opacity(){
      this.scrollY = window.scrollY;
    }
    Up_opacity.prototype.up_widen = function(ele){
      if(ele.current != null){
        this.selector = ele.current;
        this.scroll = this.selector.offsetTop - window.innerHeight;
        if(this.scrollY > this.scroll){
            this.selector.style.opacity = 1;
            this.selector.style.transform ="translateY(0px)";
            this.selector.style.width = "800px";
            this.selector_child = this.selector.children[1];
        }
      }
    } 
  return (
    <>
        <Title ref={upTitle}>
            <p>{props.p}</p>
            <h1>{props.h1}</h1>
        </Title>
    </>
  )
}

export default Uptitle